  #footer{
    font-size: 25px;
    color: blue;
    font-family: Arial;
    position: relative;
    top: 60px;
  }

  #root{
    width: 700px;
    height: 700px;
    margin: 5% auto 20px auto;
    border: 5px solid red;
    border-radius: 50%;
    text-align: center;
    background-image: radial-gradient(circle, #E7E714 10%, #DE4C2D 100%);
    box-shadow: 0 0 8px 10px hsl(20, 100%, 30%);
  }
  
  .container-fluid{
    position: relative;
    top: 120px;
    h1{
      position: relative;
      top: -20px;
      font-family: 'Zhi Mang Xing', cursive;
      font-size: 70px;
    }
  }
  
  #break-length, #session-length{
    font-size: 3em;
    font-family: 'Barlow', sans-serif;
    border: 2px solid red;
    color: #ffff80;
    background: black;
    display: inline-block;
    width: 100px;
  }
  #break-label, #session-label, #timer-label{
    font-family: 'Exo 2', sans-serif;
    font-size: 35px;
    margin-bottom: 20px;
  }
  
  $button-space: 20px;
  %length-buttons{
    font-size: 3em;
    height: 50px;
    line-height: 0px;
    width: 50px;
    border: 2px solid hsl(0, 100%, 66%);
    border-radius: 20%;
    padding-bottom: 10px;
    background: hsl(30, 100%, 90%);
    &:focus{
      outline: 0px;
    }
    &:active{
      opacity: 0.7;
      border: 2px solid hsl(0, 100%, 40%);
    }
  }
  #break-decrement, #session-decrement{
    @extend %length-buttons;
    margin-right: $button-space;
  }
  #break-increment, #session-increment{
    @extend %length-buttons;
    margin-left: $button-space;
  }
  
  #time-left{
    font-size: 70px;
    border: 5px solid red;
    border-radius: 20%;
    width: 45%;
    position: relative;
    margin: 20px auto;
    font-family: 'Orbitron', sans-serif;
    color: yellow;
    background: black;
  }
  
  #start_stop, #reset{
    margin: 20px;
    padding: 20px;
    border-radius: 50%;
    border: 3px solid hsl(0, 100%, 66%);
    background: hsl(60, 100%, 60%);
    &:focus{
      outline: 0;
    }
    &:active{
      opacity: 0.7;
      border: 3px solid hsl(0, 100%, 40%);
    }
  }
  
  